import React, {useState} from "react";
import SectionProducts from "./Section-products/Section-products";
import FilterSection from "./Filter-section/Filter-section";
import "./Main.css";
import data from "../../assets/data/data.json";

let Main = () => {
    const [newData, setNewData]=useState(data)
    const handleClick = (value) => {
        let arrayProduct = [];
        data.map( pr => {
            if (value === "all") {
                arrayProduct = data;
            } else if (pr.gender === value) {
                arrayProduct.push(pr);
            }
        })
        setNewData(arrayProduct);
    }
    return (
        <main>
            <div className="pseudoHeader"></div>
            <div className="mainHeader">
                <input type="button" className="mainHeaderItem" onClick={() => {handleClick("all")}} value="ALL"/>
                <input type="button" className="mainHeaderItem" onClick={()=> {handleClick("for him")}} value="FOR HIM"/>
                <input type="button" className="mainHeaderItem" onClick={() => {handleClick("for her")}} value="FOR HER"/>
            </div>
            <div className="mainSections">
                <FilterSection/>
                <SectionProducts data={newData}/>
            </div>

        </main>
    )
}

export default Main;
